.sidebar-container {
    height: 100%;
    width: 240px;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    background-color: $main-color;
    color: #fff;
    transition: width .5s;
}

#sidebar-toggle:checked~.sidebar-container {
    width: 60px;
}

#sidebar-toggle:checked~.sidebar-container .sidebar-header h3,
#sidebar-toggle:checked~.sidebar-container .sidebar-menu li span:last-child {
    display: none;
}

#sidebar-toggle:checked~.sidebar-container .sidebar-header,
#sidebar-toggle:checked~.sidebar-container .sidebar-menu li {
    display: flex;
    justify-content: center;
}

#sidebar-toggle:checked~.dashboard-content {
    margin-inline-start: 60px;
}

#sidebar-toggle:checked~.dashboard-content header {
    left: 60px;
    width: calc(100% - 60px);
}

.sidebar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    padding: 0rem 1rem;
}

.sidebar-menu {
    padding: 1rem;

    & li {
        margin-bottom: 1rem;
    }

    & a {
        color: #fff;
        text-decoration: none;
        font-size: .9rem;
    }

    & a span:first-child {
        padding-right: .5rem;
    }
}

@media only screen and (max-width: 1200px) {
    .sidebar-container {
        width: 60px;
        z-index: 150;
    }

    .sidebar-container .sidebar-header h3,
    .sidebar-container .sidebar-menu li span:last-child {
        display: none;
    }

    .sidebar-container .sidebar-header,
    .sidebar-container .sidebar-menu li {
        display: flex;
        justify-content: center;
    }

    .dashboard-content {
        margin-inline-start: 60px;
    }

    .dashboard-content header {
        left: 60px;
    }

    #sidebar-toggle:checked~.sidebar-container {
        width: 240px;
    }

    #sidebar-toggle:checked~.sidebar-container .sidebar-header h3,
    #sidebar-toggle:checked~.sidebar-container .sidebar-menu li span:last-child {
        display: inline;
    }

    #sidebar-toggle:checked~.sidebar-container .sidebar-header {
        display: flex;
        justify-content: space-between;
    }

    #sidebar-toggle:checked~.sidebar-container .sidebar-menu li {
        display: block;
    }

    #sidebar-toggle:checked~.dashboard-content {
        margin-inline-start: 60px;
    }

    #sidebar-toggle:checked~.dashboard-content header {
        left: 60px;
    }
}

.toggle-btn {
    display: none;
}

@media (max-width: 960px) {
    .dash-cards {
        grid-template-columns: repeat(2, 1fr);
    }

    .card-single {
        margin-bottom: 1rem;
    }
}