.panel-container {
    margin-top: 3rem;
    padding: 1rem;
    background: #fff;
    border-radius: 7px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.panel-upload {

    & input {
        display: none;
    }

    & label {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        cursor: pointer;
        gap: 12px;
    }
}

.panel-filter {
    display: flex;
    gap: 10px;
}

.search-by-form {

    & input {
        border: 1px solid $color3;
        padding: .2rem 1rem;
        border-radius: 4px;
        font-size: 1rem;
    }
}