.dashboard-content {
    position: relative;
    transition: margin-left .5s;
}

header {
    background-color: #fff;
    position: fixed;
    left: 240px;
    top: 0;
    z-index: 100;
    height: 60px;
    padding: 0rem 1rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-bottom: 1px solid lightgray;
    transition: left .5s;
    width: calc(100% - 240px);
}

.social-icons {
    display: flex;
    align-items: center;

    & span,
    & div {
        margin-inline-start: 1.2rem;
    }

    & div {
        height: 38px;
        width: 38px;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url(../../img/profile.png);
        border-radius: 50%;
    }
}

.search-container {
    display: flex;
    align-items: center;

    & input {
        border: 0;
        outline: 0;
        padding: 1rem;
        height: 38px;
    }
}

.main-content {
    margin-top: 60px;
    background-color: #f1f5f9;
    min-height: calc(100vh - 60px);
    padding: 1rem 3rem;
}

.dash-title {
    color: $color2;
    margin-bottom: 1rem;
}

.dash-cards {
    display: grid;
    grid-column-gap: 5rem;
}

.card-single {
    background-color: #fff;
    border-radius: 7px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.card-body {
    padding: 1.3rem 1rem;
    display: flex;
    align-items: center;

    & span {
        font-size: 1.5rem;
        color: #777;
        padding-inline-end: 1.4rem;
    }

    & h5 {
        color: $color3;
        font-size: 1rem;
    }

    & h4 {
        color: $color2;
        font-size: 1.1rem;
        margin-top: .5rem;
    }
}

.card-footer {
    padding: .2rem 1rem;
    background: #f9fafc;
    border-bottom-right-radius: 7px;
    border-bottom-left-radius: 7px;

    & a {
        color: $main-color
    }
}

@media (min-width: 1200px) {
    .dashboard-content {
        margin-inline-start: 240px;
    }

}

@media (max-width: 1200px) {

    header {
        width: calc(100% - 60px);

    }
}

@media (min-width: 960px) {
    .dash-cards {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 600px) {
    .dash-cards {
        grid-template-columns: 100%;

    }
}

@media (max-width: 450px) {
    .main-content {
        padding-left: 1rem;
        padding-right: 1rem;
    }
}