.main-table {
    margin-top: 3rem;
}

.transactions-card {
    background: #fff;
    border-radius: 7px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    margin-bottom: 3rem;

    & h3 {
        color: $color3;
        padding: 1rem;
    }

    & table {
        width: 100%;
        border-collapse: collapse;

        & thead {
            background: #efefef;
            text-align: left;
        }

        & td,
        & th {
            font-size: .9rem;
            padding: 1rem .5rem;
            color: $color2;
        }

        & td {
            font-size: .8rem;
        }

        & tr:nth-child(even) {
            background-color: #f9fafc;
        }
    }

}

.badge {
    padding: .5rem 1rem;
    border-radius: 20px;
    color: $main-color;
    font-weight: 700;
}

.badge.income {
    background: #DEF7EC;
}

.badge.expense {
    background: #F0F6B2;
    color: orange;
}

.responsive-table {
    overflow-x: auto;
}

.paging-btn {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    padding: .7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;

    & button {
        color: $color3;
        padding: .5rem;
        border-radius: 50%;
        transition: background-color .2s;

        &:hover {
            background-color: lightgrey;
        }
    }
}